import axios from "axios";
import jwt_decode from "jwt-decode";
import API_URL from "../constants/api-url";

const connection_url = API_URL + "auth/";

class AuthService {
  async login(username, password) {
    return axios.post(connection_url + "signin", {
      username,
      password
    }).then(response => {
      if (response.data.access_token) {
        localStorage.setItem("user", JSON.stringify(response.data));
      }
      return response.data;
    })
  }

  async register(username, password) {
    return await axios.post(connection_url + "signup", {
      username,
      password
    });
  }

  logout() {
    localStorage.removeItem("user");
  }

  getCurrentUser() {
    return JSON.parse(localStorage.getItem("user"));
  }

  logoutByTokenExpired() {
    const token = this.getCurrentUser().access_token;
    const tokenExp = jwt_decode(token).exp;
    const currentTime = Date.now() / 1000;
    if (tokenExp < currentTime) {
      this.logout();
      return true;
    }
    return false;
  }
}

export default new AuthService();