import { useState } from "react";
import authService from "../services/auth.service";
import { Box, Button, TextField, Typography } from "@mui/material";

function Register() {

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");

  const handleChangeUsername = (e) => {
    setUsername(e.target.value);
  }

  const handleChangePassword = (e) => {
    setPassword(e.target.value);
  }

  const handleRegister = (e) => {
    e.preventDefault();
    authService.register(username, password).then(
      () => {
        setMessage("Congratulations, you have successfully registered! You can now log in to your account.");
      },
      error => {
        setMessage("Registration failed. Try a different username");
      }
    );
  }

  return (<div className="form-container">
    <Typography gutterBottom variant="h4" component="h1">Sign Up</Typography>
    <form onSubmit={handleRegister}>
      <Box mb={4}>
        <TextField fullWidth variant="outlined" label="Username" onChange={handleChangeUsername} value={username} />
      </Box>
      <Box mb={4}>
        <TextField fullWidth type="password" variant="outlined" label="Password" onChange={handleChangePassword} value={password} />
      </Box>
{/*       <input placeholder="Username" onChange={handleChangeUsername} value={username}></input>
      <input type="password" placeholder="Password" onChange={handleChangePassword} value={password}></input> */}
      <Button fullWidth type="submit" variant="contained">SIGN UP</Button>
    </form>
    <span>{message}</span>
  </div>);
}

export default Register;