import { useEffect, useState } from "react";
import wordService from "../services/word.service";
import Wordle from "./wordle.component";
import { Typography } from "@mui/material";

function Game() {

  const [solution, setSolution] = useState(null);
  const [wordOfTheDay, setWordOfTheDay] = useState(null);
  const [playedToday, setPlayedToday] = useState(false);

  useEffect(() => {
    const lastPlayedDate = localStorage.getItem('lastPlayedDate');
    const today = new Date().toLocaleDateString();
    if (lastPlayedDate === today) {
      setPlayedToday(true);
    } else {
      async function setWordOfTheDayData() {
        const wordOfTheDay = await wordService.getWordOfTheDay();
        setWordOfTheDay(wordOfTheDay)
        setSolution(wordOfTheDay.word)
      }
      setWordOfTheDayData();
    }
  }, []);

  const handlePlay = () => {
    localStorage.setItem('lastPlayedDate', new Date().toLocaleDateString());
    setPlayedToday(true);
  };

  return (
    <div className="App">
      <Typography variant="h4" component="h1">Wordle Wisdom</Typography>
      {playedToday && (
        <p>Thanks for playing! Come back tomorrow.</p>
      )}
      {!playedToday && (
        <Wordle solution={solution} onPlay={handlePlay} wordOfTheDay={wordOfTheDay} />
      )}
    </div>
  )
}

export default Game;
