import React, { useEffect, useState } from 'react'
import useWordle from '../hooks/useWordle'
import Grid from './wordle/grid.component'
import keys from '../constants/keys'
import Keypad from './wordle/keypad.component'
/* import Modal from './wordle/modal.component' */
import Modal from '@mui/material/Modal'
import { Typography } from '@mui/material'
import gameService from '../services/game.service'

export default function Wordle({ solution, onPlay, wordOfTheDay }) {
  const [showModal, setShowModal] = useState(false)
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setTimeout(() => setOpen(true), 1500);
  }
  const handleClose = () => {
    setOpen(false);
    gameService.attemptGameCreation(isCorrect, turn);
    onPlay();
  }
  const { currentGuess, guesses, turn, isCorrect, usedKeys, handleKeyup } = useWordle(solution)

  useEffect(() => {
    window.addEventListener('keyup', handleKeyup)

    if (isCorrect) {
      /* onPlay()
      setTimeout(() => setShowModal(true), 1500) */
      window.removeEventListener('keyup', handleKeyup)
      handleOpen();
    }
    if (turn > 5) {
      /* onPlay()
      setTimeout(() => setShowModal(true), 1500) */
      window.removeEventListener('keyup', handleKeyup)
      handleOpen();
    }

    return () => window.removeEventListener('keyup', handleKeyup)
  }, [handleKeyup, isCorrect, turn])

  return (
    <div>
      <Grid guesses={guesses} currentGuess={currentGuess} turn={turn} />
      <Keypad keys={keys} usedKeys={usedKeys} handleKeyup={handleKeyup} />
      {/* {showModal && <Modal isCorrect={isCorrect} turn={turn} solution={solution} />} */}
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        {isCorrect ? (
          <div className='modal'>
            <Typography variant="h4" id="modal-modal-title" className='win-message'>You win</Typography>
            <Typography gutterBottom variant="body1" id="modal-modal-description">You found the word in <strong>{turn}</strong> guesses</Typography>
            {wordOfTheDay ? (
              <div className='modal-main-content'>
                <Typography gutterBottom variant="h4">{solution}</Typography>
                <Typography gutterBottom variant="body1">{wordOfTheDay.meaning}</Typography>
                <Typography gutterBottom variant="h6">Did you know?</Typography>
                <Typography variant="body1">{wordOfTheDay.interestingFact}</Typography>
              </div>
            ) : null}
          </div>
        )
        : (
          <div className='modal'>
            <Typography variant="h4" id="modal-modal-title" className='lose-message'>Not quite this time</Typography>
            <Typography gutterBottom variant="body1" id="modal-modal-description">The word was <strong>{solution}</strong></Typography>
            {wordOfTheDay ? (
              <div className='modal-main-content'>
                <Typography gutterBottom variant="h4">{solution}</Typography>
                <Typography gutterBottom variant="body1">{wordOfTheDay.meaning}</Typography>
                <Typography gutterBottom variant="h6">Did you know?</Typography>
                <Typography variant="body1">{wordOfTheDay.interestingFact}</Typography>
              </div>
            ) : null}
          </div>
        )}
      </Modal>
    </div>
  )
}
