import axios from "axios";
import authHeader from "./auth-header";
import API_URL from "../constants/api-url";

const connection_url = API_URL + "games/new";

class GameService {
  async attemptGameCreation(isWon, numAttempts) {
    if (authHeader()) {
      try {
        const response = await axios.post(connection_url, {isWon, numAttempts} , { headers: authHeader() });
        return response.data;
      } catch (error) {
        return error;
      } 
    }
  }
}

export default new GameService();