import { useState } from "react";
import authService from "../services/auth.service";
import { useNavigate } from "react-router-dom";
import { Box, Button, TextField, Typography } from "@mui/material";

function Login({ onUserChange }) {

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const navigate = useNavigate();

  const handleChangeUsername = (e) => {
    setUsername(e.target.value);
  }

  const handleChangePassword = (e) => {
    setPassword(e.target.value);
  }

  const handleLogin = (e) => {
    e.preventDefault();
    authService.login(username, password).then(
      () => {
        const user = authService.getCurrentUser();
        onUserChange(user);
        navigate("/profile");
      },
      error => {
        setMessage("Invalid Credentials");
      }
    );
  }

  return (<div className="form-container">
    <Typography gutterBottom variant="h4" component="h1">Sign In</Typography>
    <form onSubmit={handleLogin}>
      <Box mb={4}>
        <TextField fullWidth variant="outlined" label="Username" onChange={handleChangeUsername} value={username} />
      </Box>
      <Box mb={4}>
        <TextField fullWidth type="password" variant="outlined" label="Password" onChange={handleChangePassword} value={password} />
      </Box>
    <Button fullWidth type="submit" variant="contained">SIGN IN</Button>
  </form >
    <span>{message}</span>
  </div>);
}

export default Login;