import { Link, Navigate, Route, Routes } from "react-router-dom";
import Game from "./components/game.component";
import About from "./components/about.component";
import Login from "./components/login.component";
import Register from "./components/register.component";
import LeaderBoard from "./components/leaderboard.component";
import NotFound from "./components/not-found.component";
import Profile from "./components/profile.component";
import { useEffect, useState } from "react";
import authService from "./services/auth.service";
import { IconButton, Menu, MenuItem } from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import './styles/index.css';


function App() {

  const [currentUser, setCurrentUser] = useState(undefined);
  const [anchorEl, setAnchorEl] = useState(null);

  useEffect(() => {
    function checkActiveUser() {
      console.log('app running!!!!!!!!!');
      const user = authService.getCurrentUser();
      if (user) {
        setCurrentUser(user);
      }
    }

    checkActiveUser();
  }, []);

  const handleLogout = () => {
    authService.logout();
    setCurrentUser(undefined);
  }

  const handleUserChange = (user) => {
    setCurrentUser(user);
  }

  const handleMenuClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <nav className="header">
        <div className="logo-section">
          <IconButton onClick={handleMenuClick} edge="start" color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleMenuClose}
          >
            <Link to='/'><MenuItem>Play</MenuItem></Link>
            <Link to='/leaderboard'><MenuItem>LeaderBoard</MenuItem></Link>
            {currentUser ? (
              <Link to='/profile'><MenuItem>Profile</MenuItem></Link>
            ) : (
              <Link to='/login'><MenuItem>Login</MenuItem></Link>
            )}
            {currentUser ? (
              <a href="/login" onClick={handleLogout}><MenuItem>Logout</MenuItem></a>
            ) : (
              <Link to='/register'><MenuItem>Register</MenuItem></Link>
            )}
          </Menu>
          <div className="logo">Wordle Wisdom</div>
        </div>
        <ul className="nav-links">
          <li><Link to='/'>Play</Link></li>
          <li><Link to='/leaderboard'>LeaderBoard</Link></li>
          {currentUser ? (
            <>
              <li><Link to='/profile'>Profile</Link></li>
              <li><a href="/login" onClick={handleLogout}>Logout</a></li>
            </>
          ) : (
            <>
              <li><Link to='/login'>Login</Link></li>
              <li><Link to='/register'>Register</Link></li>
            </>
          )}

        </ul>
      </nav>
      <Routes>
        <Route path="/" element={<Game />} />
        <Route path="/home" element={currentUser ? <Game /> : <About />} />

        {/* Protected Routes */}
        <Route path="/profile" element={currentUser ? <Profile onUserChange={handleUserChange} /> : <Navigate to="/login" />} />

        {/* Public Routes */}
        <Route path="/login" element={currentUser ? <Navigate to="/profile" /> : <Login onUserChange={handleUserChange} />} />
        <Route path="/register" element={currentUser ? <Navigate to="/profile" /> : <Register />} />
        <Route path="/leaderboard" element={<LeaderBoard />} />
        <Route path='*' element={<NotFound />} />
      </Routes>
    </>
  );
}

export default App;
